@import url(https://fonts.googleapis.com/css?family=Montserrat);
.style_header__21AOY {
	display: flex;
	position: relative;
	align-content: space-between;
	justify-content: space-between;
	padding: 1.4em;
}

a {
	color: white;
	text-decoration: none;
}

/* Left Header portion */

.style_header-left__12n19 {
	height: 6vw;
	width: 16vw;
	display: flex;
	cursor: pointer;
	z-index: 3;
}

.style_mec__151eS {
	width: 100%;
	font-size: 1vw;
	display: flex;
	flex-direction: column;
	justify-content: center;
	font-family: 'Montserrat';
}

/* Center Header portion */

.style_header-center___FQif {
	position: absolute;
	height: 16vh;
	margin: 2vh auto;
	left: 0;
	right: 0;
	padding-bottom: 4vh;
	display: flex;
	align-content: center;
	justify-content: center;
}

.style_excel__1CnN2 {
	width: 8vw;
	font-size: 2.5vw;
	font-weight: 600;
	display: flex;
	flex-direction: column;
	justify-content: center;
}

/*Right Header portion */

.style_header-right__1_p8X {
	height: 4vw;
	width: 16vw;
	padding: 2vh;
	border-radius: 15px;
	display: flex;
	z-index: 3;
	flex-direction: row-reverse;
}

.style_header-right__1_p8X>a:hover {
	border-bottom: solid 0.3px black;
}

/* resizing the logos */

.style_logomec__9cZbp {
	max-width: 6vw;
}

.style_logoexcel__2V6RR {
	max-width: 17vw;
}

.style_logolegacy__3jUw1 {
	max-width: 4vw;
}

.style_logolegacy__3jUw1:hover {
	-webkit-transition-duration: 0.3s;
	        transition-duration: 0.3s;
    -webkit-transition-timing-function: ease-out;
            transition-timing-function: ease-out;
    -webkit-transform: translateY(-5px);
            transform: translateY(-5px);
}

/* Mobile devices */

@media only screen and (max-width: 480px) {
	.style_header__21AOY {
		display: flex;
		flex-direction: column;
		padding: 1vh;
	}
	.style_header-left__12n19 {
		display: none;
	}
	.style_header-center___FQif {
		height: 25vh;
		margin: 0vh auto;
		padding-bottom: 1vh;
	}
	.style_header-right__1_p8X {
		display: none;
		/* padding: 1vh;
		height: 12vw;
		width: 48vw;
		margin: 16vh auto 0vh auto; */
	}
	.style_logomec__9cZbp {
		max-width: 10vw;
	}
	.style_excel__1CnN2 {
		width: 18vw;
		font-size: 6vw;
	}
	.style_logoexcel__2V6RR {
		max-width: 50vw;
	}
	.style_logolegacy__3jUw1 {
		max-width: 12vw;
	}
}
